import { makeStyles } from '@mui/styles';
import { Collapse } from '@mui/material';
import { amber } from '@mui/material/colors';
import { useState, useEffect } from 'react';

const useStyles = makeStyles((theme) => ({
    root: {
        display: 'flex',
        justifyContent: 'flex-start',
        alignItems: 'flex-start',
        height: '100vh',
        fontFamily: 'Nunito',
    },
    colorText: {
        color: amber[600],
    },
    container: {
        textAlign: 'left',
    },
    title: {
        margin: '20vh 10vh 0 10vh',
        color: '#fff',
        fontSize: '3.8rem',
        [theme.breakpoints.down('md')]: {
            fontSize: '2.4rem',
            margin: '20vh 5vh 0 5vh',
        },
    },
    goDown: {
        color: '#5AFF3D',
        fontSize: '4rem',
    },
    details: {
        margin: '0 10vh 0 10vh',
        fontSize: '1.4rem',
        [theme.breakpoints.down('md')]: {
            fontSize: '1.2rem',
            margin: '0 5vh 0 5vh',

        },
    }
}));
const Hero = () => {
    const classes = useStyles();
    const [checked, setChecked] = useState(false);
    useEffect(() => {
        setChecked(true);
    }, []);
    return (
        <Collapse
            in={checked}
            {...({ timeout: 1500 })}
        >
            <div className={classes.container}>
                <h2 className={classes.title}>
                    About
                    Meta<span className={classes.colorText}>Boners.</span>
                </h2>
                <div className={classes.details}>
                    <p>Metaverse amusement park with many experiences, including a comedy club.</p>
                    <p>Get exclusive membership benefits by owning one of 10K NFTs- ticket to the park.</p>
                    <p>Play using NFTs and earn crypto by Joke to Earn, Play to Earn, and other NFT games.</p>
                    <p>Tokenomics supports nature conservation and community empowerment every month.</p>
                </div>
                {/* <Scroll to="place-to-visit" smooth={true}>
                        <IconButton>
                            <ExpandMoreIcon className={classes.goDown} />
                        </IconButton>
                    </Scroll> */}
            </div>
        </Collapse>
    )
}

export default Hero
