import { Card, CardMedia, CardContent, Typography, Grow } from "@mui/material"
import { makeStyles } from "@mui/styles"

const useStyles = makeStyles((theme) => ({
    root: {
        maxWidth: 350,
        transition: "transform 0.15s ease-in-out",
        "&:hover": { transform: "scale3d(1.05, 1.05, 1)" },
    }
}))

const Tile = ({ visible }) => {
    const classes = useStyles()
    const startPlay = (e) => {
        e.target.getElementsByTagName('video')[0].play()
    }

    const pausePlay = (e) => {
        e.target.getElementsByTagName('video')[0].load()
    }

    return (
        <Grow in={visible} {...(visible ? { timeout: 1000 } : {})}>
            <Card className={classes.root} onMouseOver={startPlay} onMouseOut={pausePlay}>
                {/* <CardActionArea > */}
                <CardMedia component="video"
                    height="250"
                    image="/assets/video.mp4"
                    alt="man VR"
                    poster="/assets/video_thumbnail.jpg"
                    sx={{ pointerEvents: "none" }}
                />
                <CardContent sx={{ pointerEvents: "none" }}>
                    <Typography gutterBottom variant="h5" component="div">
                        This Guy!
                    </Typography>
                    <Typography variant="body2" color="text.secondary">
                        This is a random sentence i typed to take up some space so that I can test the interaction. If you read till them end, Kudos!!
                    </Typography>
                </CardContent>
                {/* </CardActionArea> */}
            </Card >
        </Grow>
    )
}

export default Tile
