import { Container, Grid } from '@mui/material';
import { makeStyles } from '@mui/styles';
import useWindowPosition from '../hooks/useWindowPosition';
import Tile from './Tile';

const useStyles = makeStyles((theme) => ({
    root: {
        minHeight: '100vh',
        paddingTop: theme.spacing(5)
    },
}));

const About = () => {
    const classes = useStyles()
    const visible = useWindowPosition("about_grid")
    return (
        <Container>
            <Grid className={classes.root} justifyContent="center" direction="row" container spacing={{ xs: 2, md: 3 }} columns={{ xs: 4, sm: 8, md: 12 }}>
                {Array.from(Array(6)).map((_, index) => (
                    <Grid item xs={2} sm={4} md={4} key={index} id="about_grid">
                        <Tile visible={visible} />
                    </Grid>
                ))}
            </Grid>
        </Container>
    )
}

export default About
