import { makeStyles } from '@mui/styles';
import { AppBar, Toolbar } from '@mui/material';
import { amber } from '@mui/material/colors';
import clsx from 'clsx';

const useStyles = makeStyles((theme) => ({
    root: {
        display: 'flex',
        justifyContent: 'flex-start',
        alignItems: 'flex-start',
        fontFamily: 'Nunito',
    },
    appbar: {
        background: 'none',
        backdropFilter: 'blur(3px)'
    },
    appbarWrapper: {
        width: '100%',
        marginLeft: '10vh',
        [theme.breakpoints.down('md')]: {
            marginLeft: '5vh',
        },
    },
    appbarTitle: {
        flexGrow: '1',
    },
    icon: {
        color: '#fff',
        fontSize: '2rem',
    },
    colorText: {
        color: amber[600],
    },
    container: {
        textAlign: 'left',
    },
    title: {
        margin: '20vh 0 0 10vh',
        color: '#fff',
        fontSize: '4.5rem',
    },
    goDown: {
        color: '#5AFF3D',
        fontSize: '4rem',
    },
    details: {
        marginLeft: '10vh',
    }
}));
export default function Header() {
    const classes = useStyles();
    return (
        <AppBar className={clsx(classes.root, classes.appbar)} id="header" position="fixed" elevation={0} sx={{ backgroundColor: "rgba(0,0,0,0.1)" }}>
            <Toolbar className={classes.appbarWrapper} disableGutters={true}>
                <h1 className={classes.appbarTitle}>
                    Meta<span className={classes.colorText}>Boners.</span>
                </h1>
            </Toolbar>
        </AppBar>
    );
}